<p className="text-lg"><span className="font-bold">Copiii cu Vârsta de 13 Ani și Sub:</span> Compania recunoaște obligația specială de a proteja informațiile de identificare personală obținute de la copiii cu vârsta de 13 ani și sub. CA ATARE, DACĂ AVEȚI 13 ANI SAU MAI PUȚIN, COMPANIA VĂ SOLICITĂ SĂ NU TRIMITEȚI NICIO INFORMAȚIE PERSONALĂ PE SITE SAU CĂTRE COMPANIE. Dacă Compania descoperă că un copil de 13 ani sau mai mic s-a înregistrat pe Site sau ne-a furnizat informații de identificare personală, vom șterge informațiile de identificare ale acelui copil din înregistrările noastre. Compania încurajează totuși părinții să intre online împreună cu copiii lor. Iată câteva sfaturi pentru a face experiența online a unui copil mai sigură:</p>

